import * as i18n from '../i18n_ast';
/**
 * @abstract
 */
export abstract class Serializer {
/**
 * @abstract
 * @param {?} messages
 * @return {?}
 */
write(messages: i18n.Message[]) {}
/**
 * @abstract
 * @param {?} content
 * @param {?} url
 * @return {?}
 */
load(content: string, url: string) {}
/**
 * @abstract
 * @param {?} message
 * @return {?}
 */
digest(message: i18n.Message) {}
/**
 * @param {?} message
 * @return {?}
 */
createNameMapper(message: i18n.Message): PlaceholderMapper { return null; }
}

/**
 * A `PlaceholderMapper` converts placeholder names from internal to serialized representation and
 * back.
 *
 * It should be used for serialization format that put constraints on the placeholder names.
 */
export interface PlaceholderMapper {
  toPublicName(internalName: string): string;

  toInternalName(publicName: string): string;
}
interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
